@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  @apply bg-gray-50 text-gray-800 font-sans;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

/* Dark mode fallback (if used later) */
body.dark {
  @apply bg-black text-white;
}

/* Minimal Mode */
body.minimal {
  @apply bg-transparent p-0 m-0;
}

/* Code font */
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/* Scrollbar Styling */
* {
  scrollbar-width: thin;
  scrollbar-color: #0093a4 transparent;
}

*::-webkit-scrollbar {
  width: 8px;
}
*::-webkit-scrollbar-track {
  background: transparent;
}
*::-webkit-scrollbar-thumb {
  background-color: #0093a4;
  border-radius: 20px;
  border: 3px solid transparent;
}

/* Tooltip utility */
.tooltip-box {
  @apply absolute bottom-full left-1/2 transform -translate-x-1/2 mb-2 w-64 bg-gray-700 text-white text-sm rounded py-2 px-3 opacity-0 group-hover:opacity-100 transition-opacity duration-200 z-10 pointer-events-none;
}

/* Debug utility for layout tuning */
.debug-outline * {
  outline: 1px dashed red;
}

/* Custom utility layer for shared shadow */
@layer utilities {
  .shadow-custom {
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.35);
  }
}
